.profile {
    order: 1;
}

.profile>img {
    width: 100%;
    height: 100px;
    object-fit: cover;
    display: block;
}

.dataBox {
    display: flex;
    align-items: flex-start;
    padding: 1em 0;
    gap: 0 1em;
}

.dataBox span {
    color: #635b97;
}

.dataBox img {
    width: 20%;
}

.info {
    display: flex;
    flex-direction: column;
    font-size: 1.2rem;
}

.messages form {
    width: 100%;
    max-height: 323px;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    gap: 1em 0;
}

.inputTextBox {
    width: 100%;
}

.messagesForm {
    position: relative;
}

.messages .inputText {
    width: 50%;
    height: 40px;
    font-size: 1rem;
    padding: .5em;
}

.messages button {
    padding: 0.5em;
    align-self: center;
    font-size: 1rem;
}

.messagesBox {
    width: 28%;
    max-height: 175px;
    padding: 1em;
    overflow: auto scroll;
}

.messagesBox::-webkit-scrollbar {
    width: 10px;
    background: #e5e5e5;
}

.messagesBox::-webkit-scrollbar-thumb {
    height: 20px;
    background: #9b9b9b;
    border-radius: 50px;
}

ul {
    list-style: none;
}

.error {
    position: absolute;
    color: #f60000;
    top: 60%;
    left: 0;
}

.statusBox {
    display: flex;
    align-items: center;
    gap: 0 0.1em;
}

.status-word {
    display: inline-block;
    width: 100px;
    height: 22px;
}

@media (max-width:820px) {
    .messages {
        padding: 0 1em;
    }

    .messagesBox {
        width: 48%;
    }

    .info {
        font-size: 1rem;
    }

    .messages button {
        font-size: 0.8rem;
        padding: 0.25em;
    }

    .messagesBox li {
        font-size: 0.8rem;
    }
}

@media (max-width:430px) {
    .messages {
        padding: 0 0.5em;
    }
}